import { Location } from '@angular/common';
import { Component, ElementRef, OnInit, Renderer2 } from '@angular/core';
import { SegmentTabs, Store } from './store';
import { FormArray, FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ModalController } from '@ionic/angular';
import { TransactionDateComponent } from 'src/app/components/transaction-date/transaction-date.component';

@Component({
  selector: 'app-customer-onboarding-corporate',
  templateUrl: './customer-onboarding-corporate.page.html',
  styleUrls: ['./customer-onboarding-corporate.page.scss'],
})
export class CustomerOnboardingCorporatePage implements OnInit {
  deleteIcon = 'assets/icon/delete.svg';
  editIcon = 'assets/icon/edit.svg';

  selectedTab: string;

  primaryInfoForm: FormGroup;
  contactInfoForm: FormGroup;
  identityForm: FormGroup;
  directorDetailForm: FormGroup;

  categorySelectedOption: any;
  countrySelectedOption: any;
  ownershipSelectedOption: any;
  orgTypeSelectedOption: any;

  collapsedStates: boolean[] = [];

  isYesSelected: boolean = true;

  constructor(
    private _location: Location,
    private fb: FormBuilder,
    private modalCtrl: ModalController,
    private renderer: Renderer2,
    private elementRef: ElementRef
  ) {}

  ngOnInit() {
    this.selectedTab = this.SegmentTabsValues[0].val;
    this.createPrimaryInfoForm();
    this.createContactInfoForm();
    this.createIdentityForm();
    this.createDirectorDetailsForm();
  }

  SegmentTabsValues: SegmentTabs[] = Store.segmentButtons;

  categories = ['Product', 'Service'];
  countries = ['India', 'China'];
  states = ['New Delhi', 'UP'];
  cities = ['Bengaluru', 'Hyderabad'];
  orgTypes = ['Profit', 'Non-Profit'];
  owner = ['Owned', 'Rented'];
  prefixArr = ['Mr', 'Mrs', 'Ms'];
  genderArr = ['Male', 'Female'];
  statuses = ['Living', 'Moved-on'];
  citizenShip = ['India', 'Norway'];
  residenceCountry = ['India', 'Greenland'];
  docIds = ['Pan', 'Passport'];

  goBack() {
    this._location.back();
  }

  createPrimaryInfoForm() {
    this.contactInfoForm = this.fb.group({
      address1: ['', Validators.required],
      address2: [''],
      country: ['', Validators.required],
      zipCode: ['', Validators.required],
      state: ['', Validators.required],
      city: ['', Validators.required],
    });
  }

  createContactInfoForm() {
    this.primaryInfoForm = this.fb.group({
      userImg: [''],
      corporateId: [''],
      companyName: ['', Validators.required],
      category: ['', Validators.required],
      country: ['', Validators.required],
    });
  }
  createIdentityForm() {
    this.identityForm = this.fb.group({
      incorporationNo: ['', Validators.required],
      dateOfIssue: ['', Validators.required],
      dateOfExpiry: ['', Validators.required],
    });
  }

  createDirectorDetailsForm() {
    this.directorDetailForm = this.fb.group({
      directorDetails: this.fb.array([this.addDirectorDetailForm()]),
    });
  }

  addDirectorDetailForm() {
    return this.fb.group({
      customerId: [''],
      prefix: ['', Validators.required],
      firstName: ['', Validators.required],
      middleName: [''],
      lastName: ['', Validators.required],
      dateOfBirth: ['', Validators.required],
      gender: ['', Validators.required],
      birthPlace: ['', Validators.required],
      residenceStatus: ['', Validators.required],
      citizenshipBy: ['', Validators.required],
      residence: ['', Validators.required],
      docId: ['', Validators.required],
      docNumber: ['', Validators.required],
      address1: ['', Validators.required],
      address2: ['', Validators.required],
      country: ['', Validators.required],
      zipCode: ['', Validators.required],
      state: ['', Validators.required],
      city: ['', Validators.required],
      mobileNo: ['', Validators.required],
      landlineNo: [''],
      email: ['', Validators.required],
      taxId: ['', Validators.required],
      countryOfIssue: ['', Validators.required],
    });
  }

  get directorDetails(): FormArray {
    return this.directorDetailForm.get('directorDetails') as FormArray;
  }

  addDirectorDetailItem() {
    this.directorDetails.push(this.addDirectorDetailForm());
  }

  removeDirectorDetailItem(index: number) {
    this.directorDetails.removeAt(index);
  }

  onFileSelected(e: any) {
    const file = e.target.files[0];
    const fReader = new FileReader();
    fReader.readAsDataURL(file);
    fReader.onloadend = (_event: any) => {
      let filename = file.name;
      let base64File = _event.target.result;
      this.primaryInfoForm.get('userImg').patchValue(base64File);
    };
  }

  datePopup(data: string, idx) {
    this.modalCtrl
      .create({
        component: TransactionDateComponent,
        componentProps: {
          screenType: 'basicInfo',
        },
      })
      .then((modalResp) => {
        modalResp.present();
        modalResp.onDidDismiss().then((res) => {
          console.log(res, 'after dismiss');

          let formCtrl = this.directorDetails.at(idx);

          if (data === 'DOI')
            this.identityForm.get('dateOfIssue').setValue(res?.data);
          if (data === 'DOE')
            this.identityForm.get('dateOfExpiry').setValue(res?.data);
          if (data === 'DIR-DETAILS')
            formCtrl.get('dateOfBirth').setValue(res?.data);
        });
      });
  }

  toggleDocUpload(e) {
    e.value === 'No'
      ? (this.isYesSelected = false)
      : (this.isYesSelected = true);
  }

  saveDirFunc(index: number) {
    this.collapsedStates[index] = !this.collapsedStates[index];
    console.log(this.collapsedStates)


  }

  isCollapsed(index: number): boolean {
    return this.collapsedStates[index];
  }

  expand(index: number){
    this.collapsedStates[index] = false;
  }
}
